





























































































	import server from '../../../store/server.js';
	export default {
		onLoad() {
			this.Token=server.net.GetSessionToken();
			this.getProject();
			var today =server.net.getFormatDate(new Date());
			console.log(today);
			this.checkDate = today;
			this.ConcreteDate = today;
		},
		data() {
			return {
				projectList: [],
				projectIndex: 0,
				projectVal: '',
				projectText: '',
				gradeList: [{
					value: 'C15',
					text: 'C15'
				}, {
					value: 'C20',
					text: 'C20'
				}, {
					value: 'C25',
					text: 'C25'
				}, {
					value: 'C30',
					text: 'C30'
				}, {
					value: 'C35',
					text: 'C35'
				}, {
					value: 'C40',
					text: 'C40'
				}, {
					value: 'C45',
					text: 'C45'
				}, {
					value: 'C50',
					text: 'C50'
				}, {
					value: 'C55',
					text: 'C55'
				}, {
					value: 'C60',
					text: 'C60'
				}],
				grade: 0,
				gradeVal:'C15',
				checkDate: '',
				ConcreteDate:'',
				ComponentList: [{
					value: 0,
					text: '墙柱'
				}, {
					value: 1,
					text: '梁板'
				}],
				ComponentIndex: 0,
				ComponentVal: '',
				ComponentText:'请选择构件类型',
				TestState: '侧面',
				isAdd: true,
				tapTimes: 0,
				dataList: [],
				Floor:'',
				Temperature:''

			}
		},
		methods: {
			getProject() { //获取项目数据
				let url = server.net.setUrl('api/DropDownListApi/GetProject');
				uni.request({
					url: url,
					method: 'GET',
					header: {
						Token: this.Token
					},
					
					success: (data) => {
						server.net.showWrongTipsEx(data);
						let jsondata = data.data;
						let dataArr = jsondata.data;
						
						let first={
							value:"",
							text:"请选择项目"
						}
						dataArr.unshift(first)
						
						if (jsondata.status == 1) {
							this.projectList = this.setData(dataArr);
							this.projectVal = this.projectList[this.projectIndex].value;
							this.projectText = this.projectList[this.projectIndex].text;
						} else {
							console.log("项目获取失败" + jsondata.status);
						}
					},
					fail: (data, code) => {
						console.log("项目数据请求失败" + data + code)
					},
					complete: () => {}
				});
			},
			addDatas() {
				//this.isAdd = true;
				let addTimes = this.dataList.length + 1;
				if (addTimes < 10) {
					this.dataList.push({
						dataNum: addTimes
					});
					addTimes += 1;
				} else {
					console.log(this.dataList)
					uni.showToast({
						icon:'none',
						title: '最多添加10条'
					})
					return false;
				}
			},
			changeCheckDate(e) {
				this.checkDate = e.detail.value;
			},
			changeConcreteDate(e){
				this.ConcreteDate = e.detail.value;
			},
			changeComponent(e) {
				this.ComponentIndex = e.detail.value;
				this.ComponentVal = this.ComponentList[this.ComponentIndex].value;
				this.ComponentText=this.ComponentList[this.ComponentIndex].text;
				
			},
			changeGrade(e) {
				this.grade = e.detail.value;
				this.gradeVal = this.gradeList[this.grade].text;
			},
			changeProject(e) {
				this.projectIndex = e.detail.value;
				this.projectVal = this.projectList[e.detail.value].value;
				this.projectText = this.projectList[this.projectIndex].text;
			},
			setData: function(items) { //设置两个picker的数据
				let newItems = [];
				items.forEach((e) => {
					newItems.push({
						text: e.text,
						value: e.value
					}); 
				});
				return newItems;
			},
			submit() {
				let url = server.net.setUrl('api/QualityConcreteBackApi/AddOrUpdate');
				let newItems = [];
				for (let i in this.dataList) {
					newItems.push({
						"Name":this.dataList[i].dataNum,
						"DataAttr":this.dataList[i].dataInput.split(' ')
					});
				}
				
                if(!this.projectVal)
				{
					server.net.showWrongTips("请选择项目");
					return false; 
				}
				if(!this.Floor)
				{
					server.net.showWrongTips("请输入构件部位/楼层");
					return false; 
				}
                if(!this.Temperature)
                {
                    server.net.showWrongTips("请输入日平均温度");
                 	return false; 
                }
				var d1 = new Date(this.ConcreteDate);  
				var d2 = new Date(this.checkDate); 
                if(d2.getTime() < d1.getTime())  
                {  
					uni.showModal({
                        title: '提示',
                        content: '检查日期不能比浇筑日期早',
						showCancel:false
	                });
                    return false;  
                }
				console.log(newItems);
				let ajaxdata = {
					ProjectID: this.projectVal, //项目ID
					ProjectName:this.projectText,
					Grade: this.gradeVal,
					Floor:this.Floor,
					TestState: this.TestState,
					ConcreteDate: this.ConcreteDate, //检查日期
					CheckDate:this.checkDate,
					ComponentType:this.ComponentText,
					Temperature:this.Temperature,
					Remark:'',
					DetailCollection: newItems
				};
				console.log(ajaxdata);
				uni.request({
					url: url,
					method: 'POST',
					header: {
						Token: this.Token
					},
					data: JSON.stringify(ajaxdata),
					success: res => {
						server.net.showWrongTipsEx(res);
						let result = res.data;
						let jsondata = result.data;
						if (result.status == 1) {
							uni.showToast({
								title: '提交成功',
								mask: false,
								duration: 1500
							});
							setTimeout(function() {
								uni.navigateBack({
									delta: 1
								})
							}, 2000)
						} else {
							console.log(result.message);
							server.net.showWrongTips("请填写正确数据");
						}
					},
					fail: () => {
						console.log("实测实量提交失败");
					},
					complete: () => {}
				});
			},
			tempS(){
				console.log("暂存")
				let detailCollections = [];
				for (let i in this.dataList) {
					detailCollections.push({
						"Name":this.dataList[i].dataNum,
						"DataAttr":this.dataList[i].dataInput.split(' ')
					});
				}
				let tempObject={
					ProjectID: this.projectVal, //项目ID
					ProjectIndex: this.projectIndex,
					ProjectName:this.projectText,
					GradeIndex: this.grade,
					GradeVal:this.gradeVal,
					Floor:this.Floor,
					TestState: this.TestState,
					ConcreteDate: this.ConcreteDate, //检查日期
					CheckDate:this.checkDate,
					ComponentVal:this.ComponentVal,
					ComponentIndex:this.ComponentIndex,
					Temperature:this.Temperature,
					DetailCollection: detailCollections
				}
			    let concreteOff = uni.getStorageSync('ConcreteListOffLine');
				if (concreteOff) {
					console.log(concreteOff);
					concreteOff.push(tempObject)
				}else{
					concreteOff=[];
					concreteOff.push(tempObject)
				}
				uni.setStorageSync("ConcreteListOffLine",concreteOff);
				uni.showToast({
					title: '暂存成功',
					mask: false,
					duration: 1500
				});
				setTimeout(function() {
					uni.navigateBack({
						delta: 1
					})
				}, 2000)
				
				
			}
		}
	}



